import { useCallback, useState, useEffect } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import multicall from 'utils/multicall'
import { QuoteToken } from 'config/constants/types'
import erc20ABI from 'config/abi/erc20.json'
import BigNumber from 'bignumber.js'
import { getBoardroom, getTokenAddress } from 'utils/addressHelpers'
import boardRoom from 'config/abi/boardRoom.json'
import { useBoardroom } from './useContract'
import useRefresh from './useRefresh'

const useHarvestFromBoardroom = () => {
  const { account } = useWallet()
  const contract = useBoardroom()

  const handle = useCallback(async () => {
    const txHash = await contract.methods.claimReward().send({ from: account })
    return txHash
  }, [account, contract])

  return { onReward: handle }
}

export const useBoardroomState = () => {
  const { account } = useWallet()
  const [balance, setBalance] = useState([])
  const contract = getBoardroom()

  useEffect(() => {
    const fetchAll = async () => {
      const calls = [
        {
          address: contract,
          name: 'balanceOf',
          params: [account],
        },
        {
          address: contract,
          name: 'canWithdraw',
          params: [account],
        },
        {
          address: contract,
          name: 'canClaimReward',
          params: [account],
        },
        {
          address: contract,
          name: 'epoch',
          params: [],
        },
        {
          address: contract,
          name: 'totalSupply',
          params: [],
        },
      ]

      const [balanceOf, canWithdraw, canClaimReward, epoch, totalSupply] = await multicall(boardRoom, calls)
      setBalance([
        new BigNumber(balanceOf).div(new BigNumber(10).pow(18)),
        canWithdraw[0],
        canClaimReward[0],
        new BigNumber(epoch).toNumber(),
        new BigNumber(totalSupply).div(new BigNumber(10).pow(18)),
      ])
    }

    if (account) {
      fetchAll()
    }
  }, [account, contract])

  return balance
}

export const useEarningsOnBoardroom = () => {
  const { account } = useWallet()
  const [balance, setBalance] = useState(new BigNumber(0))
  const contract = useBoardroom()
  const { slowRefresh } = useRefresh()

  useEffect(() => {
    contract.methods
      .earned(account)
      .call()
      .then((b) => setBalance(new BigNumber(b).div(new BigNumber(10).pow(18))))
      .catch((err) => console.error(err.stack))
  }, [account, contract, slowRefresh])

  return balance
}

const zero = new BigNumber(0)

export const useSupplyState = () => {
  const [balance, setBalance] = useState([zero, zero, zero])
  const sdo = getTokenAddress(QuoteToken.SDO)
  const sso = getTokenAddress(QuoteToken.SSO)
  const sbo = getTokenAddress(QuoteToken.SBO)

  useEffect(() => {
    const fetchTotals = async () => {
      const calls = [
        {
          address: sdo,
          name: 'totalSupply',
          params: [],
        },
        {
          address: sso,
          name: 'totalSupply',
          params: [],
        },
        {
          address: sbo,
          name: 'totalSupply',
          params: [],
        },
      ]

      const [totalSdo, totalSso, totalSbo] = await multicall(erc20ABI, calls)
      setBalance([
        new BigNumber(totalSdo).div(new BigNumber(10).pow(18)),
        new BigNumber(totalSso).div(new BigNumber(10).pow(18)),
        new BigNumber(totalSbo).div(new BigNumber(10).pow(18)),
      ])
    }

    fetchTotals()
  }, [sdo, sbo, sso])

  return balance
}

export default useHarvestFromBoardroom
